.btn {
  border: none;
  background-color: #000;
  color: #fff;
  cursor: pointer;
  height: 25px;
  outline: 0;
  -webkit-transition: all 250ms ease-in-out;
  -o-transition: all 250ms ease-in-out;
  transition: all 250ms ease-in-out;
  width: 100px;
}
.btn:hover {
  background-color: #fff;
  color: #000;
}




